import {useHttp} from "./https";
import {QueryKey, useMutation, useQuery, useQueryClient} from "react-query";
import {
    useAddConfig,
    useDeleteConfig,
} from "./useOptimisticOptions";
import {Epic} from "../types";

export const useEpics = (params?: Partial<Epic>) =>{
    const client = useHttp()
    return useQuery<Epic[]>(['epics', params], () =>{
        return client('epics', {data: params})
    })
}
export const useAddEpic = (queryKey: QueryKey) => {
    const client = useHttp()
    return useMutation((params: Partial<Epic>) => client(`epics`, {
        method: 'POST',
        data: params
    }), useAddConfig(queryKey))
};
export const useDeleteEpic = (queryKey: QueryKey) => {
    const client = useHttp()
    return useMutation((id:number) => client(`epics/${id}`, {
        method: 'DELETE',
    }), useDeleteConfig(queryKey))
};

