import {Task} from "../types";
import {useHttp} from "./https";
import {QueryKey, useMutation, useQuery} from "react-query";
import {
    useAddConfig,
    useConfig,
    useDeleteConfig,
    useEditConfig,
    useReorderKanbanConfig, useReorderTaskConfig
} from "./useOptimisticOptions";
import {useUrlQueryParam} from "./url";
import {useCallback} from "react";
import {SortProps} from "./kanban";

export const useTasks = (params?: Partial<Task>) =>{
    const client = useHttp();
    return useQuery<Task[]>(['tasks', params], () =>{
        return client('tasks', {data: params});
    })
}

export const useTask = (id?:number) =>{
    const client = useHttp();
    return useQuery<Task>(['task', {id}], () =>{
        return client(`tasks/${id}`)
    }, {
        enabled: !!id
    })
}

export const useAddTask = (queryKey: QueryKey) =>{
    const client = useHttp();
    return useMutation((params: Partial<Task>) => {
        return client('tasks', {
            data: params,
            method: 'POST'
        })
    }, useAddConfig(queryKey))
}

export const useEditTask = (queryKey: QueryKey) =>{
    const client = useHttp();
    return useMutation((params: Partial<Task>) => {
        return client(`tasks/${params.id}`, {
            data: params,
            method: 'PATCH'
        })
    }, useEditConfig(queryKey))
}

export const useDeleteTask = (queryKey: QueryKey) =>{
    const client = useHttp();
    return useMutation((id: number) => {
        return client(`tasks/${id}`, {
            method: 'DELETE'
        })
    }, useDeleteConfig(queryKey))
}

export const useTaskModal = () =>{
    const [{editingTaskId}, setEditingTaskId] = useUrlQueryParam(['editingTaskId'])
    const {data: editingTask, isLoading} = useTask(Number(editingTaskId))
    const startEdit = useCallback((id: number) =>{
        setEditingTaskId({editingTaskId: id})
    }, [setEditingTaskId])

    const close = useCallback(() =>{
        setEditingTaskId({editingTaskId: ''})
    }, [setEditingTaskId])

    return {
        editingTaskId,
        editingTask,
        isLoading,
        close,
        startEdit
    }
}
export const useReorderTask = (queryKey: QueryKey) =>{
    const client = useHttp()
    return useMutation(
        (params: SortProps) =>{
            return client('tasks/reorder', {
                data: params,
                method: 'POST'
            })
        }, useReorderTaskConfig(queryKey)
    )
}
