import React, {memo, useEffect} from "react";
import {Button, Col, Drawer, DrawerProps, Form, Input, Layout, Row, Space, Spin} from "antd";
import {useAddEpic} from "../../utils/epic";
import {useEpicsQueryKey} from "./utils";
import {Content} from "antd/es/layout/layout";
import {ErrorBox} from "../../components/lib";
import {useForm} from "antd/es/form/Form";
import './style.css'
import {useProjectIdInUrl} from "../kanban/utils";


export const CreateEpic = memo((props: Pick<DrawerProps, 'visible'> & {onClose: () => void}) =>{
    const {mutateAsync: addEpic, isLoading, error} = useAddEpic(useEpicsQueryKey())
    const [form] = useForm();
    const projectId = useProjectIdInUrl();
    const onFinish = async (values: any) =>{
        await addEpic({...values, projectId})
        props.onClose()
    }
    useEffect(()=>{
        form.resetFields()
    }, [form, props.visible])
    return <Drawer visible={props.visible}
                   onClose={props.onClose}
                   forceRender={true} destroyOnClose={true}
                   width={'100%'}
                   >
        <Layout style={{height: '100vh', background: '#fff'}}>
            <Content style={{ flex: '0 0 80%', background: '#fff'}}>
                <Spin size={'large'} spinning={isLoading} wrapperClassName={'spin-height'} >
                    <Row justify={'center'} align={'middle'} style={{height:'100%'}}>
                        <Col span={12}>
                            {/*<Row align={'middle'}>*/}
                            {/*    <Col>*/}
                                    <Space direction={'vertical'} size={'middle'} style={{width: '100%'}}>
                                        <h1>创建任务组</h1>
                                        <ErrorBox error={error} />
                                        <Form form={form} onFinish={onFinish}>
                                            <Form.Item
                                                label={"名称"}
                                                name={"name"}
                                                rules={[{ required: true, message: "请输入任务组名" }]}
                                            >
                                                <Input placeholder={"请输入任务组名称"} />
                                            </Form.Item>

                                            <Form.Item >
                                                <Button
                                                    loading={isLoading}
                                                    type={"primary"}
                                                    htmlType={"submit"}
                                                >
                                                    提交
                                                </Button>
                                            </Form.Item>
                                        </Form>
                                    </Space>
                            {/*    </Col>*/}
                            {/*</Row>*/}
                        </Col>
                    </Row>
                </Spin>
            </Content>
        </Layout>
    </Drawer>
})
