import React, {memo, useCallback, useState} from "react";
import {useProjectInUrl} from "../kanban/utils";
import {useDeleteEpic, useEpics} from "../../utils/epic";
import {Button, Col, Divider, Layout, List, Modal, Row, Space} from "antd";
import {useEpicSearchParams, useEpicsQueryKey} from "./utils";
import dayjs from "dayjs";
import {useTasks} from "../../utils/task";
import {Link} from "react-router-dom";
import {Epic} from "../../types";
import {CreateEpic} from "./createEpic";

const EpicPage = memo(props => {
    const {data: currentProject} = useProjectInUrl()
    const {data: epics} = useEpics(useEpicSearchParams())
    const {data: tasks} = useTasks({projectId: currentProject?.id})
    const {mutate: deleteEpic} = useDeleteEpic(useEpicsQueryKey())
    const confirmDeleteEpic = (epic: Epic) => {
        Modal.confirm({
            title: `确定删除项目组: ${epic.name}`,
            content: '点击确定删除',
            okText: '确定',
            onOk: () => {
                deleteEpic(epic.id)
            }
        })
    }
    const [modalStatus, setModalStatus] = useState(false)
    const closeModal = useCallback(() => {
        setModalStatus(!modalStatus)
    }, [modalStatus])
    return <Layout>
        <Row justify={'space-between'} align={'middle'}>
            <Col>
                <h1>{currentProject?.name} 任务组</h1>
            </Col>
            <Col>
                <Button onClick={() => setModalStatus(!modalStatus)}>创建任务组</Button>
            </Col>
        </Row>
        <List dataSource={epics} itemLayout={'vertical'} renderItem={epic => <List.Item>
            <List.Item.Meta title={<Row justify={'start'}>
                <Col span={3}>
                    <span>{epic.name}</span>
                </Col>
                <Col>
                    <Button onClick={() => confirmDeleteEpic(epic)} type={'link'}>删除</Button>
                </Col>
            </Row>} description={<div>
                <Space size={'small'} direction={'vertical'} style={{width: '100%'}}>
                    <div>开始时间: {dayjs(epic.start).format('YYYY-MM-DD')}</div>
                    <div>结束时间: {dayjs(epic.end).format('YYYY-MM-DD')}</div>
                    {tasks?.filter(task => task.epicId === epic.id).map(task => {
                        return <Link to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                                     key={task.id}
                        >
                            <div>
                                {task.name}
                            </div>
                        </Link>
                    })}
                    <Divider style={{width: '100%'}} />
                </Space>
            </div>} />
        </List.Item>} />
        <CreateEpic visible={modalStatus} onClose={closeModal} />
    </Layout>
})
export default EpicPage
